<template>
  <div class="hello">
    <el-row :gutter="20" class="functionalArea">
      <el-col :span="6">
        <el-button-group>
          <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
          <el-button type="primary" icon="el-icon-share"></el-button>
          <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
        </el-button-group>
      </el-col>
      <el-col :span="6" :offset="6">
        <el-form>
          <el-form-item label="部门" :label-width="formLabelWidth">
            <el-select v-model="selectThemeId" @change="changeThemeFn" placeholder="请选择主题" style="width:100%;">
              <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="6">
        <el-input placeholder="请输入内容" v-model="searchTheme" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>

    <el-table
            ref="multipleTable"
            :data="tableData"
            style="width: 100%"
            :header-cell-style="headerCellStyle"
            :cell-style="cellStyle"
            @select="handleSelectionChange"
            @select-all="selectAllFn"
    >
      <el-table-column
              type="selection"
              width="55">
      </el-table-column>
      <el-table-column
              label="序号"
              width="60">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.index }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="用户"
              width="160">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.login_name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="姓名"
              width="160">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="部门"
              width="140">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.subject }}</span>
        </template>
      </el-table-column>
      <el-table-column
              label="角色"
              width="140">
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>邮箱: {{ scope.row.email }}</p>
            <p>电话: {{ scope.row.mobile }}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{ scope.row.role }}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column
              label="操作"
              min-width="240">
        <template slot-scope="scope">
          <el-button
                  size="mini"
                  @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button
                  size="mini"
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="添加用户" :visible.sync="addFormVisible" width="800px">
      <el-form :model="form">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="部门" :label-width="formLabelWidth">
              <el-input v-model="form.subject" autocomplete="off" disabled></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="用户" :label-width="formLabelWidth">
              <el-input v-model="form.login_name" placeholder="请输入用户" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="姓名" :label-width="formLabelWidth">
              <el-input v-model="form.name" placeholder="请输入姓名" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="邮箱" :label-width="formLabelWidth">
              <el-input v-model="form.email" placeholder="请输入邮箱" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话" :label-width="formLabelWidth">
              <el-input v-model="form.mobile" placeholder="请输入电话" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="角色" :label-width="formLabelWidth">
              <el-select v-model="form.role_id" placeholder="请选择角色" style="width:100%;">
                <el-option v-for="(item, index) in roleList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="编辑用户" :visible.sync="fixFormVisible" width="800px">
      <el-form :model="form1">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="部门" :label-width="formLabelWidth">
              <!--<el-input v-model="form1.subject" autocomplete="off"></el-input>-->
              <el-select v-model="form1.subject" placeholder="请选择部门" style="width:100%;">
                <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.label"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <!--<el-col :span="12">-->
            <!--<el-form-item label="用户" :label-width="formLabelWidth">-->
              <!--<el-input v-model="form.login_name" placeholder="请输入用户" autocomplete="off"></el-input>-->
            <!--</el-form-item>-->
          <!--</el-col>-->
          <el-col :span="12">
            <el-form-item label="姓名" :label-width="formLabelWidth">
              <el-input v-model="form1.name" placeholder="请输入姓名" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="邮箱" :label-width="formLabelWidth">
              <el-input v-model="form1.email" placeholder="请输入邮箱" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="电话" :label-width="formLabelWidth">
              <el-input v-model="form1.mobile" placeholder="请输入电话" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="角色" :label-width="formLabelWidth">
              <el-select v-model="form1.role_id" placeholder="请选择角色" style="width:100%;">
                <el-option v-for="(item, index) in roleList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fixFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="fixSureFn">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
const emailReg = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
const telReg = /^\d{11}$/
export default {
  name: 'ATheme',
  data () {
    return {
      selectThemeId: '',
      selectThemeName: '',

      searchTheme: '',
      tableData: [],
      themeList: [],
      roleList: [],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        subject: '',
        name: '',
        login_name: '',
        email: '',
        mobile: '',
        role_id: ''
      },
      fixFormVisible: false,
      form1: {
        id: '',
        subject: '',
        name: '',
        email: '',
        mobile: '',
        role_id: ''
      },

      selectIds: [],

      notifyPromise: Promise.resolve()
    }
  },
  watch: {
  },
  created: function () {
    this.getThemeFn()
    this.getRoleFn()
  },
  methods: {
    changeThemeFn () {
      for (let i = 0; i < this.themeList.length; i++) {
        if (this.themeList[i].val === this.selectThemeId) {
          this.selectThemeName = this.themeList[i].label
          console.log(this.selectThemeName)
          this.getUserFn(this.selectThemeName)
          break
        }
      }
    },
    addBtnFn () {
      if (this.selectThemeName) {
        this.addFormVisible = true
        this.form.subject = this.selectThemeName
      } else {
        this.notifyFn('提示', '未选择主题')
      }
    },
    addSureFn () {
      let temp = {
        login_name: this.form.login_name.replace(/(^\s*)|(\s*$)/g, ''),
        mobile: this.form.mobile.replace(/(^\s*)|(\s*$)/g, ''),
        name: this.form.name.replace(/(^\s*)|(\s*$)/g, ''),
        email: this.form.email.replace(/(^\s*)|(\s*$)/g, ''),
        role_id: this.form.role_id,
        subject: this.form.subject,

        user_id: sessionStorage.user_id
      }
      console.log(temp)
      if (temp.login_name !== '' && temp.name !== '' && temp.role_id !== '' && temp.mobile.search(telReg) === 0 && temp.email.search(emailReg) === 0) {
        this.addFn(temp)
      } else {
        if (temp.login_name === '') {
          this.notifyFn('提示', '账号输入有误')
        }
        if (temp.name === '') {
          this.notifyFn('提示', '姓名输入有误')
        }
        if (temp.role_id === '') {
          this.notifyFn('提示', '请选择用户角色')
        }
        if (temp.mobile.search(telReg) === -1) {
          this.notifyFn('提示', '请输入有效电话')
        }
        if (temp.email.search(emailReg) === -1) {
          this.notifyFn('提示', '请输入有效邮箱')
        }
      }
    },
    // 通知，解决element-ui，同时调用notify时，通知重叠的问题
    notifyFn (title, msg, type = 'warning') {
      this.notifyPromise = this.notifyPromise.then(this.$nextTick).then(() => {
        this.$notify({
          title: title,
          message: msg,
          type: type,
          dangerouslyUseHTMLString: true,
          duration: 5000,
          position: 'top-right'
        })
      })
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.subject = this.selectThemeName
      this.form1.name = row.name
      this.form1.email = row.email
      this.form1.mobile = row.mobile
      let id = ''
      this.roleList.forEach(function (val, i) {
        if (val.label === row.role) {
          id = val.val
        }
      })
      this.form1.role_id = id
      this.form1.id = row.id
    },
    fixSureFn () {
      let temp = {
        id: this.form1.id,
        mobile: this.form1.mobile.replace(/(^\s*)|(\s*$)/g, ''),
        name: this.form1.name.replace(/(^\s*)|(\s*$)/g, ''),
        email: this.form1.email.replace(/(^\s*)|(\s*$)/g, ''),
        role_id: this.form1.role_id,
        subject: this.form1.subject,

        user_id: sessionStorage.user_id
      }
      console.log(temp)
      if (temp.name !== '' && temp.subject !== '' && temp.role_id !== '' && temp.mobile.search(telReg) === 0 && temp.email.search(emailReg) === 0) {
        this.fixFn(temp)
      } else {
        if (temp.subject === '') {
          this.notifyFn('提示', '部门选着有误')
        }
        if (temp.name === '') {
          this.notifyFn('提示', '姓名输入有误')
        }
        if (temp.role_id === '') {
          this.notifyFn('提示', '请选择用户角色')
        }
        if (temp.mobile.search(telReg) === -1) {
          this.notifyFn('提示', '请输入有效电话')
        }
        if (temp.email.search(emailReg) === -1) {
          this.notifyFn('提示', '请输入有效邮箱')
        }
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选用户', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.notifyFn('提示', '已取消删除')
        })
      } else {
        this.notifyFn('提示', '未选择用户')
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除用户：' + row.name, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.notifyFn('提示', '已取消删除')
      })
    },
    async getThemeFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        this.themeList = []
        if (res.errno === '0') {
          for (let i = 0; i < res.data.subjects.length; i++) {
            this.themeList.push({
              label: res.data.subjects[i].name,
              val: res.data.subjects[i].id
            })
          }
          this.themeList.push({
            label: '所有',
            val: -1
          })

          this.selectThemeId = this.themeList[0].val
          this.selectThemeName = this.themeList[0].label
          this.getUserFn(this.selectThemeName)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getUserFn (theme) {
      try {
        let params = {
          subject: theme
        }
        let res = await this.$api.unsplash.getUsers(params)
        if (res.errno === '0') {
          this.tableData = res.data
          this.tableData = this.addSequence(this.tableData)
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getRoleFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getRoles(params)
        if (res.errno === '0') {
          console.log(1, res.data)
          let tt = []
          res.data.forEach(function (val, i) {
            tt.push({
              val: val.id,
              label: val.name
            })
          })
          this.roleList = tt
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddUser(params)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.notifyFn('提示', '增加用户成功', 'success')
          this.getUserFn(this.selectThemeName)
        } else {
          this.notifyFn('提示', '增加用户失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixUser(params)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.notifyFn('提示', '修改用户成功', 'success')
          this.getUserFn(this.selectThemeName)
        } else {
          this.notifyFn('提示', '修改用户失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearUser(params)
        console.log(res)
        if (res.errno === '0') {
          this.notifyFn('提示', '删除成功', 'success')
          this.selectIds = []
          this.getUserFn(this.selectThemeName)
        } else {
          this.notifyFn('提示', '删除失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .functionalArea{
    height: 80px;
  }
  .headerCellStyle{
    text-align: center;
  }

</style>



